'use strict';

/**
* Compute the range of a one-dimensional single-precision floating-point ndarray according to a mask, ignoring `NaN` values.
*
* @module @stdlib/stats-base-ndarray-snanmskrange
*
* @example
* var ndarray = require( '@stdlib/ndarray-base-ctor' );
* var snanmskrange = require( '@stdlib/stats-base-ndarray-snanmskrange' );
*
* var xbuf = [ 1.0, -2.0, 4.0, 2.0, NaN ];
* var x = new ndarray( 'float32', xbuf, [ 5 ], [ 1 ], 0, 'row-major' );
*
* var maskbuf = [ 0, 0, 1, 0, 0 ];
* var mask = new ndarray( 'uint8', maskbuf, [ 5 ], [ 1 ], 0, 'row-major' );
*
* var v = snanmskrange( [ x, mask ] );
* // returns 4.0
*/

// MODULES //

var main = require( './main.js' );


// EXPORTS //

module.exports = main;
